.ml-custom {
    margin-left: 0%;
  }
@media screen and (min-width: 768px) {
    .ml-custom {
        margin-left: 30%;
    }
}

  .w-custom {
    width: 100%; /* Adjust as needed */
  }
  
  @media (min-width: 640px) {
    .sm\:w-another-md {
      width: 100%; /* Adjust as needed for small screens */
    }
  }
  
  @media (min-width: 768px) {
    .md\:w-another-lg {
      width: 40%; /* Adjust as needed for medium screens */
    }
  }
  
  @media (min-width: 1024px) {
    .lg\:w-another-xl {
      width: 40%; /* Adjust as needed for large screens */
    }
  }
  
  @media (min-width: 1280px) {
    .xl\:w-another-2xl {
      width: 40%; /* Adjust as needed for extra large screens */
    }
  }